.product{
  display: grid;
  grid-template-columns: calc(100% - 30.5em) 30.5em;
  &__next{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 4em;
    bottom: 1.25em;
    @include center;
    svg{
      width: 100%;
    }
  }
  &__col{
    padding: 5em 3em 4em;
  }
  &__col2{
    padding: 6.25em 3em 2.25em 3.75em;
    background-color: $gray;
    display: flex;
    flex-direction: column;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    margin-right: .75em;
    min-width: 12.5em;
    &:last-child{
      margin: 0;
    }
    img{
      display: none;
    }
  }
}

.product-table{
  margin-top: 2.375em;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    border-bottom: 1px solid rgba($gray2,.2);
    &:last-child{
      text-align: right;
    }
  }
  th{
    padding-bottom: .625em;
    font-weight: 600;
    text-align: left;
    span{
      font-size: .75em;
      line-height: 133%;
    }
    ._view2{
      display: none;
    }
  }
  td{
    padding: .625em 0;
    color: $gray2;
    &:first-child{
      padding-right: 1em;
    }
    span{
      font-size: .875em;
      line-height: 133%;
    }
  }
  &__bottom{
    margin-top: .875em;
  }
  &__btn{
    width: 100%;
    img{
      display: none;
    }
  }
  &__hint{
    display: none;
  }
}

.gallery{
  margin-top: 1.875em;
  position: relative;
  padding: 0 9.5em;
  &__img{
    padding-bottom: 57%;
    position: relative;
    display: block;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__small{
    margin-top: 1.25em;
    overflow: hidden;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
        width: 25%;
      }
    }
  }
  .swiper{
    &-button{
      width: 1.75em;
      @include center;
      svg{
        width: 100%;
      }
      &-prev{
        left: 0em;
      }
      &-next{
        right: 0em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .product{
    grid-template-columns: 50% 50%;
    &__next{
      display: none;
    }
    &__col{
      padding: 1em 1.25em 1.75em;
    }
    &__col2{
      padding: .375em 1.25em;
    }
    .main-form{
      padding-top: 1.875em;
    }
    &__nav{
      justify-content: space-between;
    }
    &__btn{
      min-width: unset;
      color: #888C90;
      background-color: transparent;
      border: none;
      height: unset;
      padding: 0;
      font-weight: 500;
      &:hover{
        color: #888C90;
        background-color: transparent;
      }
      img{
        display: block;
        width: .75em;
        margin-right: .5em;
      }
      span{
        font-size: .625em;
      }
    }
  }

  .product-table{
    margin-top: .5em;
    &__bottom{
      margin-top: .25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__hint{
      display: block;
      font-size: .5em;
      color: $gray2;
    }
    &__btn{
      width: auto;
      color: #888C90;
      background-color: transparent;
      border: none;
      height: unset;
      padding: 0;
      font-weight: 500;
      &:hover{
        color: #888C90;
        background-color: transparent;
      }
      img{
        display: block;
        width: .625em;
        margin-right: .5em;
      }
      span{
        font-size: .625em;
      }
    }
    td,
    th{
      padding: .25em 0;
      span{
        font-size: .625em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      &:nth-child(1){
        width: 40%;
      }
    }
    td{
      &:nth-child(1){
        padding-right: 0;
      }
    }
  }

  .gallery{
    padding: 0 .5em;
    margin-top: 1.25em;
    &__big{
      padding: 0 1.5em;
    }
    &__small{
      margin-top: 1.75em;
      .swiper{
        &-container{
          width: calc(100% + .375em);
        }
        &-slide{
          padding-right: .375em;
        }
      }
    }
    .swiper-button{
      width: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .product{
    grid-template-columns: 100%;
    &__col2{
      padding: 1em 1.25em;
    }
    .main-form{
      padding-top: 2.5em;
      &__wrap{
        grid-template-columns: 100%;
        gap: .5em;
      }
    }
  }

  .product-table{
    th{
      ._view1{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    &__bottom{
      margin-top: .5em;
    }
    &__hint{
      display: none;
    }
    &__btn{
      margin-left: auto;
      color: #06c;
      &:hover{
        color: #06c;
      }
      img{
        display: none;
      }
      span{
        font-size: .5em;
      }
    }
  }
}
