.reviews{
  padding: 7.5em 3em 3em;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.875em 1.25em;
  }
  &__nav{
    margin-top: 3em;
    display: flex;
    justify-content: center;
  }
  &__link{
    padding: 0 1.8125em;
  }
}

.review-card{
  display: block;
  &__img{
    @include imgCover(53.5%);
    margin-bottom: .5em;
  }
  &__text{
    font-size: .875em;
    line-height: 150%;
    color: $gray2;
    font-weight: 500;
  }
}

.credit{
  .main__list{
    column-count: 1;
    @include grid100gap(.5em);
  }
  &__hint{
    margin-top: 1em;
  }
  .main__col2{
    position: relative;
  }
}

.credit-banner{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10.5em;
  max-width: calc(100% - 21em);
  padding: 4em 3.125em 3em;
  width: 61.5em;
  background-color: #FF0000;
  color: $white;
  &__text{
    font-weight: 800;
    font-size: 2.875em;
    line-height: 120%;
    text-transform: uppercase;
  }
}

.calc-project{
  &__hint{
    margin-top: 1em;
  }
  .fg{
    input{
      background-color: #fff;
      border-color: #fff;
    }
  }
}

.calc-form{
  padding-top: 1.5em;
  &__fields{
    @include grid100gap(.875em);
  }
  .file-input__output{
    background-color: $white;
    border-color: $white;
  }
  &__btn{
    margin-top: .875em;
    width: 100%;
  }
}

.file-input{
  display: grid;
  grid-template-columns: calc(100% - 3.75em) 2.875em;
  gap: .875em;
  &__btn{
    width: 100%;
    height: 2.875em;
    img{
      width: 1.125em;
    }
    overflow: hidden;
    input{
      width: 300px;
      height: auto;
      right: 0;
      left: unset;
    }
    span,
    .wpcf7-form-control-wrap{
      position: static;
    }
  }
  &__output{
    border: 1px solid #DADEDF;
    padding: 0 .75em;
    height: 2.875em;
    width: 100%;
    color: $black;
    display: flex;
    align-items: center;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 2.875em 2.125em 2em;
    &__grid{
      margin-top: .5em;
      grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
      gap: .5em 1.875em;
    }
    &__nav{
      margin-top: .75em;
    }
    &__link{
      height: 1.5625em;
      padding: 0 1em;
      span{
        font-size: .75em;
      }
    }
  }

  .review-card{
    &__img{
      margin-bottom: .125em;
    }
    &__text{
      font-size: .625em;
      line-height: 130%;
      span{
        display: block;
        line-height: inherit;
      }
    }
  }

  .calc-project,
  .credit{
    .main__col2{
      @media screen and (min-width: 577px){
        order: 1;
      }
    }
    &__hint{
      margin-top: .5em;
    }
  }

  .credit{
    .main__list{
      gap: .25em;
      li{
        line-height: 135%;
        margin-bottom: .25em;
      }
    }
    .main-form{
      padding-top: 1.5em;
    }
  }

  .credit-banner{
    left: 3.5em;
    max-width: calc(100% - 7em);
    padding: .75em .875em;
    &__text{
      font-size: .8125em;
    }
  }

  .calc-project{

  }

  .calc-form{
    &__fields{
      gap: .5em;
    }
    &__btn{
      margin-top: .5em;
      height: 2.125em;
      span{
        font-size: .75em;
      }
    }
  }

  .file-input{
    grid-template-columns: calc(100% - 2.625em) 2.125em;
    gap: .5em;
    &__btn{
      height: 2.125em;
      img{
        width: .8125em;
      }
    }
    &__output{
      padding: 0 .5em;
      height: 2.125em;
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .reviews{
    padding: 4.375em 1.25em 2em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1em 1.25em;
    }
    &__nav{
      margin-top: 1.75em;
    }
    &__link{
      height: 2.125em;
      padding: 0 1.125em;
    }
  }

  .credit{
    .main__col{
      padding-top: 1.25em;
    }
    .main__col2{
      min-height: 17.5em;
    }
  }

  .credit-banner{
    left: 2.5em;
    max-width: calc(100% - 5em);
    padding: .875em;
  }
}
