.catalog{
  padding-bottom: 6.625em;
  position: relative;
  &__items{
    display: grid;
    padding: 0 3em;
    grid-template-columns: repeat(4, calc(25% - 10.5em / 4));
    gap: 3.125em 3.5em;
  }
}

.catalog-top{
  position: sticky;
  top: 0;
  background-color: $white;
  z-index: 2;
  padding: 4.25em 2.875em 2.25em;
  &__desktop{
    display: flex;
    justify-content: space-between;
  }
  &__group{
    display: flex;
  }
}

.catalog-tab{
  background-color: $gray;
  height: 2.875em;
  @include center;
  width: 13.125em;
  transition: all .3s;
  color: $gray2;
  font-weight: 600;
  text-transform: uppercase;
  margin-right: .6875em;
  &:last-child{
    margin-right: 0;
  }
  &:hover,
  &._active{
    background-color: $gray2;
    color: $white;
  }
  &:hover{
    cursor: pointer;
  }
  span{
    font-size: .8125em;
    line-height: 110%;
  }
}

.not-fit{
  margin: 3.5em 0 5.875em;
  background-color: $gray;
  position: relative;
  padding: 5.5em 2em 5.25em 53em;
  &__img{
    position: absolute;
    width: 32em;
    bottom: 0;
    left: 16em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 2.25em;
    line-height: 137%;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: .03em;
    @media screen and (min-width: 577px){
      br{
        display: none;
      }
    }
  }
  &__desc{
    font-size: 1.1875em;
    line-height: 147%;
    color: $gray2;
    margin-top: .25em;
  }
}

.catalog-bottom{
  margin-top: 3.5em;
  padding: 0 2.875em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
  gap: 1.25em;
}

.catalog-card{
  padding: 1.875em 2.375em;
  min-height: 14.25em;
  background-color: $gray;
  position: relative;
  &__title{
    font-size: .8125em;
    line-height: 140%;
    letter-spacing: .03em;
    font-weight: 600;
    text-transform: uppercase;
    display: block;
  }
  &__img{
    position: absolute;
    right: .625em;
    bottom: 0;
    max-width: 19.25em;
    max-height: 12em;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding-bottom: 4.75em;
    &__items{
      grid-template-columns: repeat(4,calc(25% - 5.25em / 4));
      gap: .75em 1.75em;
      padding: 0 1.6875em;
    }
  }

  .catalog-top{
    padding: 2.8125em .625em 1.875em;
  }

  .catalog-tab{
    width: 5.5em;
    height: 1.625em;
    margin-right: .3125em;
    text-align: center;
    span{
      font-size: .5625em;
    }
  }

  .not-fit{
    margin: 2.5em 0 1.5em;
    padding: 1.5em 8.5em 1.5em 18.125em;
    &__img{
      width: 13.75em;
      left: 5.375em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
    &__desc{
      font-size: .75em;
      line-height: 150%;
      margin-top: .67em;
      br{
        display: none;
      }
    }
  }

  .catalog-bottom{
    margin-top: 2.75em;
    padding: 0 .75em;
    grid-template-columns: repeat(4,calc(25% - .9375em / 4));
    gap: .3125em;
  }

  .catalog-card{
    padding: .5em;
    min-height: 5em;
    &__title{
      font-size: .5em;
      line-height: 130%;
    }
    &__img{
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 7em;
      max-height: 4.375em;
    }
    &:nth-child(2){
      .catalog-card__title{
        max-width: 9em;
        br{
          display: none;
        }
      }
    }
    &:nth-child(4){
      .catalog-card__title{
        max-width: 14em;
        br{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    padding-bottom: 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      padding: 0 1.25em;
    }
    .contacts__bottom{
      margin-top: 5em;
    }
  }

  .catalog-top{
    padding: 3.5em .25em 2.25em;
    &__desktop{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em / 2));
      gap: .3125em;
    }
    &__group{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em / 2));
      gap: .3125em;
      &:nth-child(1){
        grid-column: 1/-1;
        grid-template-columns: repeat(4,calc(25% - .9375em / 4));
      }
    }
  }

  .catalog-tab{
    width: 100%;
    margin: 0;
  }

  .not-fit{
    margin: 2.5em 0 1.5em;
    padding: 1.25em 1.25em 1.25em 10.125em;
    &__img{
      width: 16.25em;
      left: -1.875em;
    }
    &__title{
      line-height: 150%;
    }
    &__desc{
      margin-top: .75em;
    }
  }

  .catalog-bottom{
    margin-top: 1.5em;
    padding: 0 .3125em;
    grid-template-columns: repeat(2,calc(50% - .3125em / 2));
  }
}
