.menu-section{
  .sub-menu{
    overflow-y: auto;
    position: relative;
    width: 100%;
    background-color: $white;
    height: 100%;
    padding: 6em 12.5em 2.25em;
    &__grid{
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    &__img{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 17.5em);
      background-size: cover;
      background-position: center;
    }
    .menu-item{
      margin-bottom: 1.25em;
      a{
        font-size: 1.125em;
        line-height: 133%;
        font-weight: 500;
        text-decoration: underline;
        &:hover{
          color: $gray2;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .menu-section{
    .sub-menu{
      padding: 2em 1em 2em 19.75em;
      &__grid{
        display: grid;
        grid-template-columns: 50% 50%;
        gap: 2.25em 0;
        align-items: flex-start;
        padding-top: 2.5em;
      }
      &__img{
        width: 15.875em;
        height: 100%;
      }
      .menu-item{
        margin-bottom: .75em;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          font-size: .75em;
          letter-spacing: .01em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .menu-section{
    .sub-menu{
      position: static;
      height: unset;
      padding: 2.25em 1em 0;
      column-gap: .5em;
      &__img{
        display: none;
      }
      &__grid{
        grid-template-columns: 11.25em calc(100% - 11.25em);
      }
      .menu-item a{
        font-size: .6875em;
      }
    }
  }
}
