.contacts{
  display: grid;
  grid-template-columns: 41.375em calc(100% - 41.375em);
  &__col{
    padding: 5.5em 3em 4em;
    background-color: $gray;
  }
  &__title{
    font-size: 1.125em;
    line-height: 200%;
    letter-spacing: .03em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__text{
    margin-top: .625em;
    @include grid100gap(.875em);
    max-width: 15.5em;
    .text14{
      line-height: 150%;
    }
  }
  &__img{
    width: 100%;
    display: block;
    margin-top: 2em;
  }
  &__cities{
    margin-top: 1.5em;
    column-count: 2;
    column-gap: 1em;
  }
  &__city{
    display: inline-block;
    width: 100%;
    margin-bottom: 1.125em;
    font-size: .875em;
    line-height: 150%;
    letter-spacing: .03em;
    text-decoration: underline;
    color: $gray2;
    font-weight: 600;
    transition: all .3s;
    &._active{
      font-weight: 700;
    }
    &:hover{
      cursor: pointer;
      color: $black;
    }
  }
  &__bottom{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: .25em 3.125em;
    background-color: $gray2;
    height: 2.5em;
    display: flex;
    align-items: center;
    color: $gray;
    width: 100%;
    font-weight: 200;
    &-text{
      font-size: .5625em;
      margin-right: 3em;
      &:last-child{
        margin: 0;
      }
    }
    .socials{
      position: absolute;
      right: 2.875em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__group{
    position: relative;
    .socials{
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}

.map{
  width: 100%;
  height: 100%;
}

.place-card{
  .h1{
    font-size: 1em;
    margin-bottom: .5em;
  }
}

.quiz{
  display: grid;
  grid-template-columns: 31.75em calc(100% - 31.75em);
  &__col{
    padding: 7em 2.875em 4em;
    background-color: $gray;
    display: flex;
    flex-direction: column;
  }
  &__col2{
    align-self: center;
    padding-bottom: 3.75em;
  }
  &__desc{
    font-size: .875em;
    line-height: 150%;
    margin-top: 1em;
  }
  &:not(._show-results){
    ._view2{
      display: none;
    }
  }
  &._show-results{
    .quiz__col{
      justify-content: center;
      padding-right: 1em;
    }
    ._view1{
      display: none;
    }
  }
}

.quiz-form{
  margin-top: auto;
  padding-top: 1em;
  textarea{
    background-color: $white;
    border-color: $white;
  }
}

.quiz-content{
  width: 66.125em;
  max-width: 100%;
  margin: 0 auto;
  &--small{
    width: 48.125em;
  }
  .h1{
    margin-bottom: 2em;
  }
  &__title{
    text-transform: uppercase;
    font-size: 1.125em;
    line-height: 160%;
    color: $gray2;
    font-weight: 600;
  }
  &__items{
    margin-top: 1.6875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
  }
  &__btn{
    margin-top: 1em;
    width: 100%;
  }
  &__items2{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
}

.quiz-card{
  position: relative;
  .m-check{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 0 .6875em .5em 0;
    label{
      padding-left: 1.25em;
    }
  }
  &__img{
    @include imgCover(57.3%);
  }
  &__progress{
    margin-top: .625em;
    display: flex;
    align-items: center;
    height: 1.125em;
    background-color: $gray;
    padding: 0 .3125em;
    color: $white;
    font-weight: 500;
    position: relative;
    &-num{
      font-size: .875em;
      position: relative;
      z-index: 2;
    }
    &-fill{
      position: absolute;
      height: 100%;
      max-width: 100%;
      background-color: $gray2;
      top: 0;
      left: 0;
    }
  }
}

.quiz-card2{
  background-color: $gray;
  label{
    display: flex;
    align-items: center;
    padding: .8125em 1em .8125em 4.125em;
    min-height: 2.875em;
    color: $gray2;
    &:before {
      left: 1em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  span{
    font-size: .875em;
    line-height: 150%;
  }
}

.quiz-card3{
  display: grid;
  grid-template-columns: 8.5em calc(100% - 8.5em);
  align-items: center;
  .quiz-card__progress{
    margin: 0;
    height: 2.875em;
    padding: 0 .75em;
    &-num{
      font-size: .875em;
    }
  }
  &__title{
    font-weight: 500;
    color: $gray2;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    position: relative;
    grid-template-columns: 50% 50%;
    &__col{
      padding: .875em 1.25em 3em;
    }
    &__wrap{
      display: grid;
      grid-template-columns: calc(100% - 7.75em) 7.5em;
      gap: .25em;
      margin-top: .25em;
      align-items: flex-start;
    }
    &__img{
      margin: 0;
    }
    &__text{
      margin: 0;
      gap: .5em;
    }
    &__cities{
      margin-top: 2em;
    }
    &__city{
      font-size: .75em;
      margin-bottom: .5em;
    }
    &__bottom{
      padding: .3125em 1.25em;
      height: unset;
      display: block;
      &-text{
        font-size: .4375em;
        margin-right: 2em;
        display: inline;
        line-height: 120%;
      }
      .socials{
        display: none;
      }
    }
    .socials{
      display: none;
    }
  }

  .quiz{
    grid-template-columns: 15.5em calc(100% - 15.5em);
    &__col{
      padding: 1.25em;
      justify-content: center;
    }
    &__col2{
      padding: 2.5em .625em;
    }
    &__desc{
      font-size: .75em;
      margin-top: .5em;
    }
  }

  .quiz-form{
    display: none;
  }

  .quiz-content{
    .h1{
      margin-bottom: 1em;
    }
    &__title{
      font-size: .875em;
    }
    &__items{
      margin-top: .5em;
      grid-template-columns: repeat(4,calc(25% - 3em / 4));
      gap: 1em;
    }
    &__items2{
      margin-top: .75em;
      gap: .25em;
    }
    &__btn{
      margin-top: .5em;
      height: 2.125em;
      span{
        font-size: .75em;
      }
    }
  }

  .quiz-card{
    .m-check{
      padding-right: .25em;
      padding-bottom: .1875em;
      input {
        &:checked+label {
          &::before {
            background-size: .5625em auto;
          }
        }
      }
      label{
        padding-left: .8125em;
        min-height: .8125em;
        &:before{
          width: .8125em;
          height: .8125em;
        }
      }
    }
    &__progress{
      margin-top: .125em;
      height: .625em;
      padding: 0 .125em;
      &-num{
        font-size: .5em;
      }
    }
  }

  .quiz-card2{
    label{
      padding: .3125em .3125em .3125em 2.25em;
      min-height: 1.625em;
      &:before {
        left: .3125em;
      }
    }
    span{
      font-size: .75em;
    }
  }

  .quiz-card3{
    grid-template-columns: 7.625em calc(100% - 7.625em);
    .quiz-card__progress{
      height: 1.5em;
      padding: 0 .3125em;
      &-num{
        font-size: .5625em;
      }
    }
    &__title{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    grid-template-columns: 100%;
    &__col2{
      order: -1;
    }
    .map{
      height: 20em;
    }
    &__col{
      padding: .875em 1.25em 2.5em;
    }
    &__bottom{
      padding: .5em 1.25em;
      position: static;
      &-text{
        display: block;
      }
    }
  }

  .quiz{
    grid-template-columns: 100%;
    gap: 4em;
    padding: 4.125em 1.25em 3em;
    &__col{
      padding: 0;
      background-color: transparent;
    }
    &__col2{
      padding: 0;
    }
    .h1{
      br{
        display: none;
      }
    }
    &._show-results{
      gap: 2.5em;
      .quiz__col{
        order: 2;
        text-align: center;
        align-items: center;
      }
    }
  }

  .quiz-content{
    .h1{
      margin-bottom: 3.75em;
    }
    &__title{
      max-width: 80%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .625em .75em;
    }
    &__btn{
      margin-top: 1em;
    }
  }

  .quiz-card3{
    grid-template-columns: 8.625em calc(100% - 8.625em);
    .quiz-card__progress{
      height: 2.75em;
      padding: 0 .5em;
      &-num{
        font-size: .875em;
      }
    }
    &__title{
      font-size: .875em;
    }
  }
}
