.popular{
  padding: 6.25em 6em 2em;
  &__items{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 10.5em / 4));
    gap: 3.125em 3.5em;
    @media screen and (min-width: 993px){
      padding: 0 3em;
    }
  }
  &__nav{
    margin-top: 4.5em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    min-width: 12.5em;
  }
}

.slide{
  svg{
    @include centerAbsolute;
  }
}

.home-card{
  text-align: center;
  font-weight: 500;
  color: $gray2;
  &__img{
    @include imgCover(65%);
    margin-bottom: .625em;
    img{
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 100%;
      @include centerAbsolute;
    }
  }
}

.channel{
  .main__list{
    column-count: 1;
  }
  &__nav{
    margin-top: auto;
    @include grid100gap(1.125em);
    padding-top: 2em;
  }
  .main__col2{
    order: unset;
    padding: 7em 7em 3.75em;
    align-self: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.375em / 4));
    gap: 1.125em;
  }
}

.channel-card{
  display: block;
  &__img{
    @include imgCover(53.7%);
  }
}


@media screen and (max-width: 992px){
  .popular{
    padding: 3em 1.625em 1em;
    &__items{
      grid-template-columns: repeat(4,calc(25% - 5.25em / 4));
      gap: .75em 1.75em;
      margin-top: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      min-width: 9.25em;
    }
  }

  .home-card{
    &__img{
      margin-bottom: .25em;
    }
  }

  .channel{
    padding: .75em 2.5em 4em;
    position: relative;
    grid-template-columns: 100%;
    gap: 1em;
    .main__col{
      background-color: transparent;
      padding: 0;
    }
    @media screen and (min-width: 577px) {
      &__nav{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        padding: .75em 2.5em;
        grid-template-columns: repeat(2,calc(50% - .4375em));
        gap: .875em;
      }
      &__top{
        display: grid;
        grid-template-columns: calc(100% - 13.75em) 10em;
        gap: 3.75em;
      }
      .h1{
        max-width: 26.5em;
        br{
          display: none;
        }
      }
      .main__list{
        margin: 0;
        li{
          font-size: .625em;
        }
      }
    }
    .main__col2{
      padding: 0;
    }
    &__items{
      grid-template-columns: repeat(4,calc(25% - 2.625em / 4));
      gap: .625em .875em;
    }
    .channel-card:nth-child(8)~.channel-card{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .popular{
    padding: 2.625em 1.25em 2em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 2em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .channel{
    padding: 1.25em;
    .h1{
      max-width: 100%;
    }
    .main__list{
      margin-top: 1em;
    }
    &__nav{
      gap: .5em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .4375em));
    }
  }
}
