.estimate{
  display: grid;
  grid-template-columns: 31.75em calc(100% - 31.75em);
  background-color: $gray !important;
  &__col{
    padding: 8.5em 3em 3em;
    display: flex;
    flex-direction: column;
    height: $vh;
    position: sticky;
    top: 0;
  }
  &__col2{
    padding-top: 4em;
  }
  &__desc{
    margin-top: 1em;
    max-width: 21.25em;
    font-weight: 500;
    color: $gray2;
  }
}

.smeta-table-head{
  font-weight: 600;
  &__col{
    display: flex;
    align-items: center;
    &:nth-child(1){
      padding-left: 1.5em;
    }
    &:nth-child(2),
    &:nth-child(3),
    &:last-child{
      justify-content: center;
      text-align: center;
    }
    &:last-child{
      padding-right: 1.75em;
    }
    span{
      font-size: .875em;
    }
  }
}

.smeta-item-head,
.smeta-item-table-head{
  background-color: $white;
  display: grid;
  grid-template-columns: calc(100% - 31em) 9em 12em 10em;
  min-height: 2.625em;
}

.smeta-item{
  margin-top: .125em;
  &__inner{
    @include grid100gap(.125em);
  }
}

.smeta-item{
  &.active{
    .smeta-item-head{
      background-color: $gray2;
      color: $white;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='17' height='12' viewBox='0 0 17 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L8.49948 10L16 1' stroke='white' stroke-width='2' stroke-miterlimit='10'/%3E%3C/svg%3E");
    }
  }
}

.smeta-item-head{
  min-height: 1.875em;
  color: $gray2;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='17' height='12' viewBox='0 0 17 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L8.49948 10L16 1' stroke='%234B4F53' stroke-width='2' stroke-miterlimit='10'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-size: .875em auto;
  background-position: right .75em center;
}

.options{
  background-color: $white;
  padding: 1.25em 4em;
  display: none;
  .row{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
    margin-bottom: 1.25em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.popup_vub,
.pricess,
.smeta-num,
.price_str{
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup_vub{
  padding-left: 1.5em;
  justify-content: unset;
  .text_aaa{
    @include truncate(1);
  }
}

.smeta-num{
  justify-content: unset;
  padding-left: 2.5em;
}

.price_str,
.td_m_kv,
.text_aaa{
  font-size: .875em;
}

.field_calc{
  font-size: .875em;
  width: 5.14em;
  height: 2em;
  text-align: center;
  background-color: $gray;
  border: none;
  color: #4B4F53;
  margin-right: .33em;
}

.price_str{
  padding-right: 2em;
}

.options-item{
  background-color: $gray;
  position: relative;
  padding: .1875em;
  height: 100%;
  &.active{
    background-color: $gray2;
    color: $white;
  }
  &:before{
    content: '';
    position: absolute;
    width: calc(100% - .375em);
    height: 14.25em;
    background-color: $white;
    top: .1875em;
    left: .1875em;
  }
  img{
    height: 14.25em;
    width: 100%;
    object-fit: cover;
    display: block;
    position: relative;
    z-index: 2;
    margin-bottom: .5em;
  }
  .desc-field-calc,
  .project-inner-price{
    font-size: .875em;
    line-height: 120%;
    padding-left: .9em;
    padding-right: .9em;
  }
  .project-inner-price{
    font-weight: 600;
    margin-bottom: .5em;
  }
  .desc-field-calc{
    padding-bottom: .8em;
  }
}

.smeta_transport{
  margin-top: .25em;
  .smeta-item-head{
    grid-template-columns: calc(100% - 10em) 10em;
  }
}

.smeta-item-result{
  min-height: 2.75em;
  padding-top: 0 !important;
  border: none !important;
  margin-top: .25em !important;
  grid-template-columns: 100%;
  font-weight: 600;
  background-image: unset;
  .smeta-item__inner{
    grid-template-columns: calc(100% - 10em) 10em;
    gap: 0;
  }
  .popup_vub{
    font-size: 1em;
  }
  .price_str{
    //text-align: right;
    font-size: 1em;
    padding-right: 1.75em;
    white-space: nowrap;
    .price_str{
      display: block;
      padding-right: 0;
    }
  }
  .smeta-num{
    padding: 0;
  }
}


@media screen and (max-width: 992px){
  .estimate{
    grid-template-columns: 100%;
    padding: .3125em;
    &__col2{
      padding: 0;
    }
    &__col{
      position: static;
      height: unset;
      padding: 0 0 .5em;
    }
    .main-form{
      display: none;
    }
    &__top{
      display: flex;
      align-items: center;
    }
    .h1{
      flex-shrink: 0;
    }
    &__desc{
      max-width: 23.5em;
      margin-top: 0;
      margin-left: 1.5em;
      .text14{
        font-size: .5625em;
      }
    }
  }

  .smeta-item-head,
  .smeta-item-table-head{
    grid-template-columns: calc(100% - 16.5em) 5.5em 5.5em 5.5em;
  }

  .smeta-item-table-head{
    min-height: 1.875em;

  }

  .smeta-table-head{
    &__col {
      span {
        font-size: .75em;
      }
      &:nth-child(1){
        padding-left: .375em;
      }
      &:last-child{
        padding-right: 0;
      }
    }
  }

  .smeta-item-head{
    padding: .125em 0;
    min-height: 1.5em;
    background-image: unset !important;
  }

  .price_str,
  .td_m_kv,
  .text_aaa{
    font-size: .75em;
  }

  .popup_vub{
    padding-left: .375em;
  }

  .field_calc{
    font-size: .75em;
    height: 1.5em;
    width: 4em;
  }

  .price_str{
    padding-right: .375em;
  }

  .smeta-item{
    margin-top: .125em;
    &__inner{
      gap: .125em;
    }
  }

  .smeta-num{
    padding-left: 0;
  }

  .smeta-item-result{
    grid-template-columns: 100% !important;
    .price_str,
    .popup_vub{
      font-size: .75em;
      .price_str{
        font-size: 1em;
      }
    }
    .price_str{
      padding-right: .5em;
    }
  }

  .smeta-item-result .smeta-item__inner,
  .smeta_transport .smeta-item-head{
    grid-template-columns: calc(100% - 5.5em) 5.5em;
  }

  .options{
    padding: .375em .375em 1em;
    .row{
      grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      gap: .625em;
      margin-bottom: .625em;
    }
  }

  .options-item{
    &:before{
      height: 8.25em;
      width: calc(100% - .25em);
      top: .125em;
      left: .125em;
    }
    img{
      height: 8.25em;
    }
    .desc-field-calc,
    .project-inner-price{
      font-size: .625em;
      padding-left: .6em;
      padding-right: .6em;
    }
    .desc-field-calc{
      padding-bottom: .2em;
    }
  }
}


@media screen and (max-width: 576px){
  .estimate{
    padding-top: .5em;
    &__col{
      padding: 0;
    }
    &__top{
      display: block;
      padding: 0 .875em .625em;
      background-image: url(../img/rotate.svg);
      background-repeat: no-repeat;
      background-size: 2.125em auto;
      background-position: right 1em top;
    }
    &__desc{
      margin: .25em 0 0;
      .text14{
        font-size: .625em;
      }
    }
  }

  .price_str,
  .td_m_kv,
  .text_aaa{
    font-size: .5625em;
  }

  .smeta-item-head,
  .smeta-item-table-head{
    grid-template-columns: calc(100% - 11em) 3.5em 3.5em 4em;
  }

  .smeta-item-result .smeta-item__inner,
  .smeta_transport .smeta-item-head{
    grid-template-columns: calc(100% - 4em) 4em;
  }

  .smeta-table-head{
    &__col {
      span {
        font-size: .5625em;
      }
    }
  }

  .field_calc{
    font-size: .5625em;
    height: 2em;
    width: 3.44em;
  }

  .smeta-item-result .popup_vub,
  .smeta-item-result .price_str{
    font-size: .5625em;
  }

  .options-item{
    &:before{
      height: 4em;
      width: calc(100% - .125em);
      top: .0625em;
      left: .0625em;
    }
    img{
      height: 4em;
      margin-bottom: .25em;
    }
    .desc-field-calc,
    .project-inner-price{
      font-size: .5em;
      padding-left: .4em;
      padding-right: .4em;
    }
    .desc-field-calc{
      padding-bottom: .5em;
      hyphens: auto;
    }
  }
}
