.main{
  display: grid;
  grid-template-columns: 31.875em calc(100% - 31.875em);
  &__col{
    background: $gray;
    padding: 6.875em 2.875em 2.625em;
    display: flex;
    flex-direction: column;
  }
  &__list{
    color: $gray2;
    margin-top: 2em;
    column-count: 2;
    column-gap: 1em;
    li{
      display: inline-block;
      width: 100%;
    }
  }
  &__img{
    width: 100%;
    display: block;
    min-height: 100%;
    object-fit: cover;
  }
}

.m-list{
  padding-left: .5em;
  li{
    position: relative;
    padding-left: 1em;
    font-size: .875em;
    font-weight: 500;
    line-height: 150%;
    margin-bottom: .5em;
    &:before{
      content: '•';
      position: absolute;
      top: 0;
      left: 0;
      background-image: unset;
      width: unset;
      height: unset;
    }
  }
}

.main-form{
  margin-top: auto;
  padding-top: 3em;
  &__text{
    color: $gray2;
    margin-bottom: .5em;
  }
  &__title{
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.125em;
    line-height: 133%;
  }
  &__desc{
    font-weight: 500;
    font-size: .75em;
    line-height: 133%;
  }
  .fg{
    input{
      background-color: $white;
      border-color: $white;
    }
  }
  &__btn{
    width: 100%;
    margin-top: .875em;
  }
  &__hint{
    font-weight: 200;
    margin-top: .5em;
    color: $gray2;
    max-width: 22.5em;
  }
}

.we-build{
  padding: 5.25em 12.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .h1{
    text-align: center;
  }
  &__video{
    margin-top: 1.5em;
    video{
      width: 100%;
      object-fit: cover;
    }
  }
}

.advantages{
  padding: 10.5em 1em 1em;
  &__logo{
    width: 12.5em;
    margin: 0 auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__grid{
    margin: 8em auto 0;
    width: 75.75em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 8em 1.25em;
    transform: translateX(5em);
  }
}

.advantage-item{
  max-width: 12.5em;
  &__top{
    height: 2.625em;
    margin-bottom: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-weight: 600;
    font-size: 2.25em;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
  &__desc{
    font-size: .875em;
    line-height: 150%;
    font-weight: 500;
    color: $gray2;
  }
}


@media screen and (max-width: 992px){
  .main{
    grid-template-columns: 50% 50%;
    &__col2{
      order: -1;
    }
    &__col{
      padding: 1em 1.25em;
    }
    .h1{
      max-width: 15em;
    }
    &__list{
      margin-top: .5em;
    }
  }

  .main-form{
    &__title{
      font-size: .875em;
    }
    &__desc{
      font-size: .5em;
    }
    &__wrap{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .125em));
      gap: .25em;
    }
    &__btn{
      margin: 0;
      height: 2.125em;
      span{
        font-size: .75em;
      }
    }
    &__hint{
      max-width: 17.5em;
      margin-top: .25em;
    }
  }

  .we-build{
    padding: 0 2.25em;
    @media screen and (min-width: 577px){
      position: relative;
      background-color: $black;
      .h1{
        text-align: center;
        position: absolute;
        top: 3.375em;
        left: 3.75em;
        color: $white;
      }
      &__video{
        margin-top: 0;
      }
    }
  }

  .advantages{
    padding: 1.375em 1.625em 2em 2.25em;
    &__logo{
      width: 7.125em;
    }
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: 12em 12.5em 12em 6.625em;
      justify-content: space-between;
      gap: 2em 0;
      transform: unset;
    }
  }

  .advantage-item{
    &:nth-child(3n){
      max-width: 9.5em;
    }
    &__top{
      height: 2em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: .75em;
      line-height: 133%;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    grid-template-columns: 100%;
    &__col{
      padding: 1.625em 1.25em .75em;
    }
  }

  .main-index{
    padding-top: 3.125em;
    display: flex;
    flex-direction: column;
    min-height: $vh;
    .main__col{
      flex-grow: 1;
    }
    .main__img{
      min-height: 20em;
    }
    .main-form{
      padding-top: 1em;
    }
  }

  .we-build{
    padding: 6em 0;
    min-height: $vh;
    &__video{
      margin-top: 1em;
      display: flex;
      flex-direction: column;
      align-items: center;
      &:after{
        content: '';
        width: 3.75em;
        height: 4.0625em;
        background-image: url(../img/rotate.svg);
        @include bgImgSize(100%);
        margin-top: 6em;
      }
    }
  }

  .advantages{
    padding: 3em .875em 2em;
    &__logo{
      width: 8.375em;
    }
    &__grid{
      margin-top: 3em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 3.25em 1em;
    }
  }

  .advantage-item{
    text-align: center;
  }
}
