.advantages2{
  @media screen and (min-width: 993px){
    padding-top: 12.5em;
    .advantages__grid{
      margin-top: 9.5em;
    }
  }
}

.about{
  @include center;
  background-color: $gray !important;
  &__inner{
    display: grid;
    grid-template-columns: 26.125em calc(100% - 27.5em);
    gap: 1.375em;
    width: 84.0625em;
    max-width: 100%;
  }
  &__img{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
  &__items{
    @include grid100gap(1.125em);
    min-height: 100%;
  }
}

.about-card{
  padding: 1.25em 1.5em 1.5em;
  background-color: $white;
  &__title{
    font-size: 1.625em;
    line-height: 138%;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: .5em;
  }
  &__desc{
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 3.1875em 1.375em 2.5em;
    &__inner{
      grid-template-columns: 11.125em calc(100% - 11.125em - .75em);
      gap: .75em;
    }
    &__items{
      gap: .375em;
    }
    &__img{
      min-height: 16em;
      object-fit: cover;
    }
  }

  .about-card{
    padding: .625em .8125em;
    &__title{
      font-size: .875em;
      line-height: 100%;
      margin: 0;
      font-weight: 700;
    }
    &__desc{
      margin-top: .5em;
      font-size: .625em;
      line-height: 130%;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    padding: 4.75em 1.25em 4.25em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      gap: .75em;
    }
    &__img{
      min-height: unset;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
}
