.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2.875em;
  z-index: 10;
  background-color: $gray2;
  &__logo{
    width: 8em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.mob-menu__close,
.mob-menu__contacts,
.burger{
  display: none;
}

.nav{
  .menu{
    display: flex;
    &>li{
      margin-right: .625em;
      &:last-child{
        margin-right: 0;
      }
      &>a{
        color: $white;
        height: 3.75em;
        display: flex;
        align-items: center;
        padding: 0 1em;
        text-transform: uppercase;
        font-weight: 600;
        letter-spacing: .03em;
        &:hover{
          background-color: $black;
        }
      }
    }
  }
  .menu-item-has-children{
    .sub-menu{
      overflow-y: auto;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: $white;
      height: calc(100 * var(--vh) - 3.75em);
      padding: 2.25em 12.5em;
      &__grid{
        width: 100%;
        display: flex;
        justify-content: space-between;
      }
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: calc(100% - 13.5em);
        background-image: url(../img/menu-bg.jpg);
        background-size: cover;
        background-position: center;
      }
    }
    .menu-item{
      margin-bottom: 1.25em;
      a{
        font-size: 1.125em;
        line-height: 133%;
        font-weight: 500;
        text-decoration: underline;
        &:hover{
          color: $gray2;
        }
      }
    }
    @media screen and (min-width: 577px){
      &>a{
        cursor: pointer;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          width: 100%;
          height: 5px;
          bottom: -2px;
          left: 0;
        }
      }
      .sub-menu{
        opacity: 0;
        pointer-events: none;
        transition: all .3s;
        display: block !important;
      }
      &:hover{
        &>a{
          background-color: $black;
        }
        .sub-menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  height: 100%;
  &__phone{
    font-size: 1em;
    letter-spacing: .05em;
    font-weight: 600;
    color: $white;
  }
  &__whatsapp{
    margin-left: 1.25em;
    display: flex;
    align-items: center;
    background-color: #57C35A;
    padding: 0 .875em;
    height: 2.3125em;
    border-radius: 2.4375em;
    color: $white;
    &:hover{
      color: $white;
      background-color: $black;
    }
    img{
      margin-right: .5em;
      width: 1.1875em;
    }
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 0 1.25em;
    &__logo{
      width: 5.25em;
    }
  }

  .nav{
    .menu{
      &>li{
        margin-right: 0;
        &>a{
          font-size: .5625em;
          height: 4.44em;
        }
      }
    }
    .menu-item-has-children{
      .sub-menu{
        height: calc(100 * var(--vh) - 2.5em);
        padding: 2em 1em 2em 19.75em;
        &__grid{
          display: grid;
          grid-template-columns: 50% 50%;
          gap: 2.25em 0;
          align-items: flex-start;
        }
        &:before{
          width: 15.875em;
          height: 100%;
        }
      }
      .menu-item{
        margin-bottom: .75em;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          font-size: .75em;
          letter-spacing: .01em;
        }
      }
    }
  }

  .header-action{
    display: flex;
    align-items: center;
    height: 100%;
    &__phone{
      width: .875em;
      height: .875em;
      color: transparent;
      background-image: url(../img/phone.svg);
      @include bgImgSize(100%);
      &:hover{
        color: transparent;
      }
    }
    &__whatsapp{
      margin-left: .875em;
      justify-content: center;
      padding: 0;
      height: 1.625em;
      width: 1.625em;
      border-radius: 50%;
      img{
        margin: 0;
        width: .875em;
      }
      span{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    height: 3.125em;
    &__logo{
      width: 6.625em;
      @include centerAbsolute;
    }
    &._open{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .mob-menu{
    position: absolute;
    top: 0;
    left: 0;
    background-color: $gray2;
    width: 100%;
    height: $vh;
    padding-top: 3.125em;
    overflow-y: auto;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__contacts{
      display: block;
      &-link{
        font-size: .75em;
        @include center;
        height: 4.17em;
        color: $gray22;
        font-weight: 600;
        letter-spacing: .03em;
        &:hover{
          color: $white;
        }
      }
    }
    &__close{
      position: absolute;
      top: 0;
      left: 0;
      padding: 0 1.375em;
      height: 3.125em;
      display: flex;
      align-items: center;
      color: $white;
      font-weight: 600;
      text-transform: uppercase;
      img{
        width: .875em;
        margin-right: .625em;
      }
      span{
        font-size: .75em;
        letter-spacing: .03em;
      }
    }
  }

  .nav{
    .menu{
      display: block;
      &>li{
        &>a{
          justify-content: center;
          font-size: .75em;
          height: 4.17em;
        }
      }
    }
    .menu-item-has-children{
      .sub-menu{
        display: none;
        position: static;
        height: unset;
        padding: 1.25em 1em;
        column-gap: .5em;
        &:before{
          display: none;
        }
        &__grid{
          grid-template-columns: 11.25em calc(100% - 11.25em);
        }
      }
      .menu-item a{
        font-size: .6875em;
      }
    }
  }

  .burger{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: $white;
    text-transform: uppercase;
    span{
      font-size: .75em;
      letter-spacing: .03em;
    }
    img{
      width: 1em;
      margin-right: .625em;
    }
  }

  .header-action{
    &__phone{
      width: 1.0625em;
      height: 1.0625em;
    }
    &__whatsapp{
      width: 1.9375em;
      height: 1.9375em;
      margin-left: .9375em;
      img{
        width: 1.0625em;
      }
    }
  }
}
