@media screen and (min-width: 993px){
  html:has(.parallax-scroll),
  .parallax-scroll,
  .parallax-scroll .parallax-slider,
  .parallax-scroll .slide,
  .parallax-scroll .bg .parallax-scroll .wrapper_page {
    margin: 0;
    width: 100%;
    height: $vh;
    overflow: hidden;
  }

  .parallax-scroll .slide {
    position: absolute !important;
    top: 0;
    left: 0;
    transform: translate3d(0, 0, 0);
    transition: 800ms;
    background-color: $white;
  }

  .parallax-scroll .slide.benten {
    padding-top: 60px;
  }

  .parallax-scroll .bg {
    transition: 800ms;
  }

  .parallax-scroll .queue {
    transform: translate3d(0, 100%, 0);
  }

  .parallax-scroll .prev {
    transform: translate3d(0, -50%, 0);
  }

  .parallax-scroll .prev .bg {
    opacity: 0.5;
  }

  .parallax-scroll .bounce {
    transition: 300ms;
  }

  .parallax-scroll .slide:first-child.bounce {
    transform: translate3d(0, 10%, 0);
  }

  .parallax-scroll .slide:last-child.bounce {
    transform: translate3d(0, -10%, 0);
  }
}


