.home-calc{
  display: grid;
  grid-template-columns: 50% 50%;
  &__col{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__col{
    padding: 8.875em 6.25em 5em;
  }
  &__col2{
    background: #eaecec;
    padding: 8em 6.5em 2em;
  }
  &__rows{
    margin-top: 2.25em;
    @include grid100gap(.875em);
  }
}

.calc-point{
  display: grid;
  grid-template-columns: 12.25em calc(100% - 12.5em);
  gap: .25em;
}

.point-head{
  padding: .5em .875em;
  display: flex;
  align-items: center;
  background-color: $gray;
  text-transform: uppercase;
  font-weight: 600;
  min-height: 3.5em;
  span{
    font-size: .75em;
  }
}

.point-btns{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
  gap: .875em .5em;
  a{
    background-color: $gray;
    padding: .375em .25em;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .active{
    background-color: #B2B7BC;
    .price{
      color: $white;
    }
  }
  .hideblock{
    display: none;
  }
  .text{
    font-size: .75em;
    line-height: 117%;
    font-weight: 500;
    color: $gray2;
    hyphens: auto;
  }
  .price{
    font-size: .8125em;
    line-height: 107%;
    font-weight: 500;
    color: #888C8F;
    margin-top: auto;
    padding-top: .4em;
  }
}

.total-price{
  padding: .5em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 3.5em;
  background-color: $gray;
  margin-top: .75em;
}

.price-total,
.price-text{
  font-size: 1em;
  line-height: 120%;
  font-weight: 600;
}

.recommendation{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__content{
    margin-top: 2.25em;
    @include grid100gap(1.5em);
  }
  &__video{
    video{
      width: 100%;
      max-height: 19em;
      object-fit: cover;
    }
  }
}


@media screen and (max-width: 992px){
  .home-calc{
    grid-template-columns: 100%;
    &__col{
      padding: 1em .25em;
      .h1{
        text-align: center;
      }
    }
    &__col2{
      padding: 1em 1.25em;
    }
  }

  .total-price{
    background-color: #B3B8BD;
    padding: .5em;
    min-height: 2.625em;
    .price-total,
    .price-text{
      font-size: .6875em;
    }
  }

  .point-btns{
    a{
      justify-content: center;
      height: 2.75em;
      padding: .125em;
    }
    .price,
    .text{
      font-size: .625em;
    }
    .price{
      margin: 0;
    }
  }


  @media screen and (min-width: 577px){
    .home-calc{
      &__rows{
        margin-top: .75em;
        grid-template-columns: repeat(9,calc(100% / 9 - 1.5em / 9));
        gap: .1875em;
      }
    }

    .calc-point{
      grid-template-columns: 100%;
      gap: .1875em;
      &--big{
        grid-column: span 2;
        .point-btns{
          grid-template-columns: repeat(2,calc(50% - .1875em / 2));
        }
      }
    }

    .point-head{
      height: 2.75em;
      text-align: center;
      justify-content: center;
      span{
        font-size: .5em;
      }
    }

    .point-btns{
      grid-template-columns: 100%;
      gap: .1875em;
    }
  }

  .point-head{
    min-height: unset;
  }

  .recommendation{
    position: relative;
    padding-bottom: 3em;
    &__top{
      justify-content: center;
      text-align: center;
    }
    &__content{
      margin-top: 1.25em;
      gap: 1.75em;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;
      height: 1.5em;
      padding: 0 1.25em;
      span{
        font-size: .75em;
      }
    }
    .channel-card:nth-child(8)~.channel-card{
      display: none;
    }
  }
}



@media screen and (max-width: 576px){
  .home-calc{
    &__col{
      padding: 1.25em .375em;
    }
    &__rows{
      margin-top: .875em;
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .calc-point{
    grid-template-columns: 100%;
    gap: .375em;
  }

  .point-head{
    background-color: transparent;
    padding: 0;
    justify-content: center;
    text-align: center;
    span{
      font-size: .625em;
    }
  }

  .point-btns{
    a{
      min-height: 2.5em;
      height: unset;
    }
  }

  .total-price{
    margin-top: 1em;
  }

  .recommendation{
    &__video{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
    }
  }
}
