@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.875em;
    font-weight: 600;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .8125em;
        letter-spacing: 0.03em;
    }
}

.m-btn-black{
    background: #000;
    color: $white;
    &:hover{
        background: $gray2;
        color: $white;
    }
}

.m-btn-light-gray{
    background: $gray;
    color: $gray2;
    &:hover{
        background: $black;
        color: $white;
    }
}

.m-btn-gray{
    background: $gray2;
    color: $white;
    &:hover{
        background: $black;
        color: $white;
    }
    &:disabled{
        opacity: .7;
        pointer-events: none;
    }
}

.m-btn-gray-outline{
    border: 1px solid $gray2;
    color: $gray2;
    &:hover{
        background: $gray2;
        color: $white;
    }
}

.m-btn-red{
    background: #f90000;
    color: $white;
    &:hover{
        background: $gray2;
        color: $white;
    }
}
